import './App.css';
import axios from 'axios'
import React, { Component } from 'react'
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Input from '@material-ui/core/Input';
import CloudUploadIcon from '@material-ui/icons/CloudUpload';
import SendIcon from '@material-ui/icons/Send';
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';

import Button from '@material-ui/core/Button'




export default class App extends Component {
  constructor(props) {
    super(props);
    
    this.state = {loaderState:"none",Poids : 0 , Penality : 0 };
  }

  
  sendFileForFirstDraw = (event)=>{
    const url = 'http://localhost:5000/upload_first_time';
    const formData = new FormData();
    const file = event.target.files[0]
    formData.append('graphe',file)
    const config = {
        headers: {
            'content-type': 'multipart/form-data'
        }
    }
    axios.post(url, formData,config).then((response)=> {
      const network = response.data
      this.drawNetwork(network)
    })
  }
  
  sendFileForSteinerResult=(event)=>{
    // draw loading
    this.setState({loaderState:"block"})
    const url = 'http://localhost:5000/upload_steiner_result';
    
    axios.get(url).then((response)=> {
      const receivedData =response.data
      const nodes = receivedData['nodes']
      const edges = receivedData['edges']
      const addedNodes = receivedData['added_nodes']
      const network = {}
      network['edges']=edges
      network['nodes']=nodes
      network['added_nodes'] = addedNodes
      console.log('network', network)
      // undraw loading
      this.setState({loaderState:"none"})
      this.setState({Poids : receivedData['poids'],Penality:receivedData['penality']})
      this.drawSteinerResult(network)
      
      // draw()
    })

  }
  
  drawNetwork = (network)=>{
    const nodes = network.nodes.map((node)=>{
      return {id : node, label : node.toString(),group: 0}
    })
    const edges = network.edges.map((edge)=>{
      return {from: edge[0],to: edge[1],label: edge[2].toString()}
    })

    // create a network
    var container = document.getElementById("mynetwork");
    var data = {
      nodes: nodes,
      edges: edges
    };
    var options = {
      nodes: {
        shape: "ellipse",
        size: 30,
        font: {
          size: 16,
          color: "#ffffff",
          align:"center"
        },
        borderWidth: 2
      },
      edges: {
        width: 2
      }
    };
    network = new window.vis.Network(container, data, options);


  }

  drawSteinerResult = (network)=>{
    const initialNodes = network.nodes.map((node)=>{
      return {id : node, label : node.toString(),group: 1}
    })
    const addedNodes = network.added_nodes.map((node)=>{
      return {id: node, label : node.toString(), color :'red'}
    })
    const nodes = addedNodes.concat(initialNodes)
    const edges = network.edges.map((edge)=>{
      return {from: edge[0],to: edge[1],label: edge[2].toString()}
    })

    // create a network
    var container = document.getElementById("mynetwork");
    var data = {
      nodes: nodes,
      edges: edges
    };
    var options = {
      nodes: {
        shape: "ellipse",
        size: 30,
        font: {
          size: 16,
          color: "#ffffff",
          align:"center"
        },
        borderWidth: 2
      },
      edges: {
        width: 2
      }
    };
    const drawed_network = new window.vis.Network(container, data, options);
    window.drawed_network = drawed_network


  }

  
  render() {
    console.log(this.state.loaderState)

    return (
      <div>
        <AppBar position="relative">
        <Toolbar style = {{itemAlign: "center"}}>
          
          </Toolbar>
        <Typography variant="h6" color="inherit" align='center' noWrap >
            Graph web App
          </Typography>
        <Toolbar style = {{itemAlign: "center"}}>
          
        </Toolbar>
        </AppBar>

        <Container style={{marginTop:"2em"}}>
        <Grid container alignItems="center">
        <Grid item xs={6}>
        <Button 
            variant="contained"
            color="default"

            startIcon={<CloudUploadIcon />}
            fullWidth={true}
        >
        <Input type="file"
          id="avatar" name="graphe"
          accept=".txt" onChange={this.sendFileForFirstDraw}
          fullWidth={true}
          style={{opacity:0, position:"absolute"}}
          />  
        Upload graph file here
      </Button>
        </Grid>
        <Grid item xs={6}>
        <Button 
            variant="contained"
            color="primary"

            endIcon={<SendIcon></SendIcon>}
            onClick={this.sendFileForSteinerResult}
            fullWidth={true}
        >
        Steiner Result

      </Button>
        </Grid>
        </Grid>
        
      
      
  
        <div id="mynetwork">
        <div className="ui active centered inline loader" style={{display:this.state.loaderState}}></div>
        </div>
        <Grid container alignItems="center">
        <Grid item xs={6}>
        <Button 
          variant="outlined"
          color="primary"
          style={{textAlign: 'center'}}
          fullWidth={true}>Poids={this.state.Poids}</Button>
        </Grid>
        <Grid item xs={6}>
          <Button 
          variant="outlined"
          color="primary"
          style={{textAlign: 'center'}}
          fullWidth={true}>Penalit√©={this.state.Penality}</Button>
          
        </Grid>
        </Grid>
       
      </Container>
      </div>


    )
  }
}


